<template>
	<div class="container">
		<video :src="videoPath" :autoplay="true" :controls="true"></video>
		<button @click="nextVideo" class="next-button">下一首</button>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				videoList: [
					'/static/gyc.mp4',
					'/static/lx.mp4',
					'/static/ws.mp4',
					'/static/yg.mp4',
					'/static/myty.mp4',
					'/static/tkzc.mp4',
					'/static/wbzd.mp4',
					'/static/cxylk.mp4',
				],
				currentVideoIndex: 0,
			};
		},
		computed: {
			videoPath() {
				return this.videoList[this.currentVideoIndex];
			},
		},
		methods: {
			nextVideo() {
				this.currentVideoIndex = (this.currentVideoIndex + 1) % this.videoList.length;
			},
		},
	};
</script>

<style>
	.container {
		width: 100vw;
		height: 100vh;
		display: flex;
		justify-content: center;
		align-items: center;
		background: linear-gradient(#00ffff 0%, #ffaaff 80%);
	}

	.next-button {
		position: fixed;
		bottom: 20px;
		left: 50%;
		transform: translateX(-50%);
	}
</style>